.promo {
  background-color: #073042;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 75px 70px;
}

.promo__link {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: white;
  text-decoration: none;
  width: 129px;
  height: 36px;
  border: 1px solid #FFFFFF;
  border-radius: 3px;
  text-align: center;
  line-height: 36px;
  box-sizing: border-box;
}

.promo__link:hover{
  cursor: pointer;
    transform: scale(1.03);
    opacity: 0.8;
}

.promo__container {
  display: flex;
  flex-direction: column;
}

.promo__title {
  display: block;
  margin: 0 0 36px 0;
  font-size: 50px;
  font-weight: 400;
  line-height: 1.16;
  max-width: 640px;
}

.promo__subtitle {
  display: block;
  max-width: 310px;
  font-weight: 400;
  font-size: 13px;
  line-height: 1.23;
  opacity: 0.5;
  margin-bottom: 146px;
  margin-top: 0;
}

.promo__img {
  min-width: 310px;
  height: 304px;
  color: white;
}

@media screen and (max-width: 1050px) {
  .promo {
    flex-direction: column-reverse;
    align-items: center;
    padding: 140px 50px 76px;
  }

  .promo__container {
    align-items: center;
  }

  .promo__title {
    max-width: 680px;
    text-align: center;
    font-size: 40px;
    line-height: 1.25;
    margin-bottom: 22px;
  }

  .promo__subtitle {
    margin-top: 0;
    margin-bottom: 92px;
    text-align: center;
    max-width: 260px;
  }

  .promo__img {
    margin-bottom: 49px;
  }
}
@media screen and (max-width: 680px) {
  .promo {
    padding: 70px 10px 30px;
  }

  .promo__title {
    font-size: 30px;
    line-height: 1.27;
    margin-bottom: 24px;
    letter-spacing: -0.04em;
  }

  .promo__subtitle {
    margin-bottom: 80px;
  }

  .promo__img {
    min-width: 210px;
    height: 206px;
    margin-bottom: 44px;
  }
}
